.nav-wrapper { 
    display: flex;
    justify-content: space-between;
    padding: 30px;

    .left-side { 
        display: flex;
    }

    .nav-link-wrapper{
        height: 22px;
        border-bottom: 1px solid transparent;
        transition: 0.5s ease-in-out;
        margin-right: 20px;
        text-transform: uppercase;
        font-size: 0.9rem;

        a {
            color: $grey;
            text-decoration: none;
            &:hover {
                color: black;
            }
        }

        &:hover {
            border-bottom: 1px solid black;
        }

        .nav-link-active { 
            border-bottom: 1px solid black;
            color: black;
            padding-bottom: 5px;
        }
    }
    .right-side {
        a { 
            cursor: pointer;
            padding-left: 15px;
            font-size: 1rem;
            color: $teal;
            transition: 0.5 ease-in-out;

            &:hover { 
                color: $dark-teal;
            }
        }

    }
}



