//mixins can be overridden by specific css classes

@mixin base-btn {
    cursor: pointer;
    height: 42;
    font-weight: 500;
    border: 1px solid transparent;
    transition: all 0.5s ease-in-out;
    background-color: $teal;
    color: white;

    &:active, &:focus { 
        outline: none;

    }

    &:hover { 
        background-color: $dark-teal;
    }
}
@mixin input-element { 
    input {
        font-family: "Bitter", sans-serif;
        color: $charcoal;
        padding: 5px 0px;
        width: 100%; 
        border-top: 0px;
        border-right: 0px;
        border-left: 0px;
        border-bottom: 1px solid $teal;
        background-color: transparent;
        transition: 0.5s all ease-in-out;
        font-size: 1.1em;
        margin-bottom: 21px;
        
        &:focus {
            outline: none;
            border-bottom: 1px solid $dark-teal;
        }
    }
}
@mixin base-grid() {
    display: grid;
    grid-gap: 21px
}